.nav {
    position: relative; padding: 2rem 0;
}
.menuBtn{
    font-size: 1.5em;
}
.active, .staic { 
    position: absolute; 
    top: 100%;
    right: 0;
    width: 10rem;
    text-align: center;
    font-size: 1.3rem;
    transition: all 0.6s;
    overflow: hidden;
    height: 8rem;
    box-shadow: 0 0 5px 2px #ddd;
    background: #fff;
}
.staic { 
    height: 0; 
    overflow: hidden; 
    transition: all 0.6s;
    box-shadow: 0 0 0px 0px #ddd;
}
.menuLi {
    padding: 1rem 0;
}