.search {
    background: #f7f7f7;
    padding : 0.8em 1em;
    border-radius : 1em;
    display: flex;
    align-items: center;
    box-shadow: 0 0 5px 2px #ddd;
}
.searchIcon {
    font-size:1em;
    cursor: pointer;
}
.input{
    width:400px;
    background: #f7f7f7;
    font-size:1em;
    border: none;
    outline: none;
}
.input::placeholder {
    font-size: 0.9rem;
    text-align: center;
}